.grid {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr;
  grid-template-columns: 1fr;
  grid-column-gap: $spacing-horizontal-lg;
  grid-row-gap: $spacing-vertical-lg;

  @media screen and (min-width: map-get($bp, 'lg')) {
    grid-column-gap: $spacing-horizontal-xl;
  }

  @media screen and (min-width: map-get($bp, 'xl')) {
    grid-column-gap: 6em;
  }

  &--half {
    -ms-grid-rows: 100% $spacing-vertical-lg 100%;

    @media screen and (min-width: map-get($bp, 'sm')) {
      -ms-grid-columns: 1fr $spacing-horizontal-lg 1fr;
      -ms-grid-rows: 100%;
      grid-template-columns: 1fr 1fr;
    }
  }

  &--two-thirds {
    -ms-grid-rows: 100% $spacing-vertical-lg 100%;

    @media screen and (min-width: map-get($bp, 'lg')) {
      -ms-grid-columns: 60% $spacing-horizontal-lg 1fr;
      -ms-grid-rows: 100%;
      grid-template-columns: 60% 1fr;
    }

    @media screen and (min-width: map-get($bp, 'xl')) {
      -ms-grid-columns: 60% ($spacing-horizontal * 6) 1fr;
    }
  }

  &--two-thirds-reverse {
    -ms-grid-rows: 100% $spacing-vertical-lg 100%;

    @media screen and (min-width: map-get($bp, 'lg')) {
      -ms-grid-columns: 1fr ($spacing-horizontal * 6) 60%;
      -ms-grid-rows: 100%;
      grid-template-columns: 1fr 60%;
    }
  }

  &--card {
    @media screen and (min-width: map-get($bp, 'md')) {
      -ms-grid-columns: 1fr $spacing-horizontal-lg 1fr;
      grid-template-columns: 1fr 1fr;
      grid-column-gap: $spacing-horizontal-xl;
      padding-bottom: 12em;
    }

    @media screen and (min-width: map-get($bp, 'lg')) {
      -ms-grid-columns: 1fr ($spacing-horizontal * 6) 1fr;
      grid-column-gap: ($spacing-horizontal * 6);
    }

    .grid__item:not(:last-child) {
      margin-bottom: $spacing-vertical-lg;

      @media screen and (min-width: map-get($bp, 'md')) {
        margin-bottom: $spacing-vertical-xl;
      }
    }

    .grid__item:nth-child(even) {

      @media screen and (min-width: map-get($bp, 'md')) {
        position: relative;
        top: 12em;
      }
    }
  }

  &--statements {
    @media screen and (min-width: map-get($bp, 'sm')) {
      grid-row-gap: $spacing-vertical * 3;
    }

    @media screen and (min-width: map-get($bp, 'md')) {
      -ms-grid-columns: 1fr $spacing-horizontal-lg 1fr;
      -ms-grid-rows: 100% $spacing-vertical-lg 100%;
      grid-template-columns: 1fr 1fr;
    }

    @media screen and (min-width: map-get($bp, 'lg')) {
      -ms-grid-columns: 1fr $spacing-horizontal-xl 1fr;
      grid-row-gap: $spacing-vertical-xl;
    }

    @media screen and (min-width: map-get($bp, 'xl')) {
      -ms-grid-columns: 1fr 6em 1fr;
    }
  }

  &--profiles {
    -ms-grid-rows: 100% $spacing-vertical-lg 100% $spacing-vertical-lg 100% $spacing-vertical-lg 100%;

    @media screen and (min-width: map-get($bp, 'sm')) {
      -ms-grid-columns: 1fr $spacing-horizontal * 6 1fr;
      -ms-grid-rows: 100% $spacing-vertical-xl 100%;
      grid-column-gap: $spacing-horizontal * 6;
      grid-row-gap: $spacing-vertical-xl;
      grid-template-columns: 1fr 1fr;
    }

    @media screen and (min-width: map-get($bp, 'lg')) {
      -ms-grid-columns: 1fr $spacing-horizontal-xl 1fr $spacing-horizontal-xl 1fr $spacing-horizontal-xl 1fr;
      -ms-grid-rows: 100%;
      grid-column-gap: $spacing-horizontal-xl;
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    @media screen and (min-width: map-get($bp, 'xl')) {
      grid-column-gap: $spacing-horizontal * 6;
    }
  }

  &--tile {
    -ms-grid-columns: 1fr;
    -ms-grid-rows: 100%;
    grid-row-gap: 1em;
    margin-bottom: $spacing-vertical;

    @media screen and (min-width: map-get($bp, 'md')) {
      -ms-grid-columns: 1fr $spacing-horizontal 1fr;
      -ms-grid-rows: 100%;
      grid-column-gap: 1em;
      grid-template-columns: 1fr 1fr;
    }
  }

  &--footer {
    -ms-grid-columns: 1fr;
    -ms-grid-rows: 100% $spacing-vertical-lg 100%;
    grid-column-gap: 4em;

    @media screen and (min-width: map-get($bp, 'lg')) {
      -ms-grid-columns: 1fr $spacing-horizontal-lg 1fr;
      -ms-grid-rows: 100%;
      grid-template-columns: 1fr 1fr;
    }
  }

  // IE 11 CSS Grid fallbacks due to no auto-placement
  &__item {
    .grid--half & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'sm')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }
      }

      &:nth-child(2) {
        -ms-grid-row: 3;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'sm')) {
          -ms-grid-row: 1;
          -ms-grid-column: 3;
        }
      }
    }

    .grid--two-thirds & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }
      }

      &:nth-child(2) {
        -ms-grid-row: 3;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 3;
        }
      }
    }

    .grid--two-thirds-reverse & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }
      }

      &:nth-child(2) {
        -ms-grid-row: 3;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 3;
        }
      }
    }

    .grid--card & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;
      }

      &:nth-child(2) {
        -ms-grid-row: 1;
        -ms-grid-column: 3;
      }

      &:nth-child(3) {
        -ms-grid-row: 2;
        -ms-grid-column: 1;
      }

      &:nth-child(4) {
        -ms-grid-row: 2;
        -ms-grid-column: 3;
      }
    }

    .grid--statements & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'md')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }
      }

      &:nth-child(2) {
        -ms-grid-row: 2;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'md')) {
          -ms-grid-row: 1;
          -ms-grid-column: 3;
        }
      }

      &:nth-child(3) {
        -ms-grid-row: 3;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'md')) {
          -ms-grid-row: 3;
          -ms-grid-column: 1;
        }
      }

      &:nth-child(4) {
        -ms-grid-row: 4;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'md')) {
          -ms-grid-row: 3;
          -ms-grid-column: 3;
        }
      }
    }

    .grid--profiles & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'sm')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }
      }

      &:nth-child(2) {
        -ms-grid-row: 3;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'sm')) {
          -ms-grid-row: 1;
          -ms-grid-column: 3;
        }

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 3;
        }
      }

      &:nth-child(3) {
        -ms-grid-row: 5;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'sm')) {
          -ms-grid-row: 3;
          -ms-grid-column: 1;
        }

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 5;
        }
      }

      &:nth-child(4) {
        -ms-grid-row: 7;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'sm')) {
          -ms-grid-row: 3;
          -ms-grid-column: 3;
        }

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 7;
        }
      }
    }

    .grid--tile & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'md')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }
      }
    }

    .grid--footer & {
      &:nth-child(1) {
        -ms-grid-row: 1;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 1;
        }
      }

      &:nth-child(2) {
        -ms-grid-row: 3;
        -ms-grid-column: 1;

        @media screen and (min-width: map-get($bp, 'lg')) {
          -ms-grid-row: 1;
          -ms-grid-column: 3;
        }
      }
    }
  }
}



