#transitioner {
  height: 0;
  opacity: 0;
  position: absolute;
  transition: opacity .3s ease-in;
  z-index: 110;

  &.active {
    background: #fff;
    height: 100%;
    width: 100%;
  }

  &.opaque {
    opacity: 1;
  }
}



